'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { Search } from "lucide-react";

interface MedicineItem {
  id: string;
  itemCode: string;
  medicineName: string;
  company: string;
  genericName: string;
  gst: string;
  billCode: string;
  itemShort: string;
  rack: string;
  shelf: string;
  type: string;
  ingredients: string;
  cess: string;
  discount: string;
  rol: string;
  packItem: string;
  hsnCode: string;
  purchaseRate: string;
  salesRate: string;
  lastBill: string;
  lastPurchase: string;
  quantity: string;
}

export default function MedicineInventory() {
  const router = useRouter();
  const [medicines, setMedicines] = useState<MedicineItem[]>([]);
  const [selectedMedicine, setSelectedMedicine] = useState<MedicineItem | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Fetch medicines from backend API on component mount
  useEffect(() => {
    async function fetchMedicines() {
      setLoading(true);
      setError(null);
      try {
        const res = await fetch('https://localhost:7112/api/Medicines');
        if (!res.ok) {
          throw new Error(`Error fetching medicines: ${res.statusText}`);
        }
        const data: MedicineItem[] = await res.json();
        setMedicines(data);
      } catch (err: any) {
        setError(err.message || 'Unknown error');
      } finally {
        setLoading(false);
      }
    }

    fetchMedicines();
  }, []);

  const handleNewClick = () => {
    router.push('/medicines/addMedicine');
  };

  const handleEditClick = () => {
    if (selectedMedicine) {
      router.push(`/medicines/addMedicine?edit=${selectedMedicine.id}`);
    }
  };

  const handleRowClick = (medicine: MedicineItem) => {
    setSelectedMedicine(medicine);
  };

  const filteredMedicines = medicines.filter(medicine =>
    medicine.medicineName.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="min-h-screen bg-gray-100 p-4">
      {/* Top Navigation */}
      <div className="bg-white rounded-lg shadow-sm p-4 mb-4">
        <div className="flex justify-between items-center">
          <div className="flex gap-3">
            <button
              onClick={handleNewClick}
              className="bg-red-500 hover:bg-red-600 text-white px-4 py-2 rounded text-sm font-medium"
            >
              New
            </button>
            <button
              onClick={handleEditClick}
              className="bg-orange-500 hover:bg-orange-600 text-white px-4 py-2 rounded text-sm font-medium"
              disabled={!selectedMedicine}
              title={!selectedMedicine ? "Select a medicine to edit" : undefined}
            >
              Edit
            </button>
          </div>

          {/* Search Box */}
          <div className="border border-gray-300/50 rounded-lg p-2 bg-gray-50 shadow-sm w-full max-w-md">
            <h3 className="text-sm font-medium text-gray-700 mb-1">Search By</h3>
            <div className="flex items-center gap-2">
              <Search className="w-4 h-4 text-gray-500" />
              <input
                type="text"
                placeholder="Search medicine name..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="flex-1 border border-gray-300 rounded-md px-2 py-1 text-sm focus:outline-none focus:ring-1 focus:ring-blue-400"
              />
            </div>
          </div>
        </div>
      </div>

      {/* Error and Loading states */}
      {error && (
        <div className="text-red-600 mb-4">
          Error: {error}
        </div>
      )}

      {loading ? (
        <div className="text-gray-600 text-center py-10">Loading medicines...</div>
      ) : (
        <div className="bg-white rounded-lg shadow-sm overflow-hidden">
          <table className="w-full">
            <thead className="bg-cyan-100">
              <tr>
                <th className="border border-gray-300 px-4 py-3 text-left text-sm font-medium text-gray-700">Item Code</th>
                <th className="border border-gray-300 px-4 py-3 text-left text-sm font-medium text-gray-700">Medicine Name</th>
                <th className="border border-gray-300 px-4 py-3 text-left text-sm font-medium text-gray-700">Company</th>
                <th className="border border-gray-300 px-4 py-3 text-left text-sm font-medium text-gray-700">Type</th>
                <th className="border border-gray-300 px-4 py-3 text-left text-sm font-medium text-gray-700">GST</th>
              </tr>
            </thead>
            <tbody>
              {filteredMedicines.map((medicine) => (
                <tr
                  key={medicine.id}
                  onClick={() => handleRowClick(medicine)}
                  className={`cursor-pointer hover:bg-gray-50 ${
                    selectedMedicine?.id === medicine.id ? 'bg-blue-800 text-white' : ''
                  }`}
                >
                  <td className="border border-gray-300 px-4 py-3 text-sm">{medicine.itemCode}</td>
                  <td className="border border-gray-300 px-4 py-3 text-sm">{medicine.medicineName}</td>
                  <td className="border border-gray-300 px-4 py-3 text-sm">{medicine.company}</td>
                  <td className="border border-gray-300 px-4 py-3 text-sm">{medicine.type}</td>
                  <td className="border border-gray-300 px-4 py-3 text-sm">{medicine.gst}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}
